/* src/styles/Dashboard.css */
.dashboard-container {
  padding: 20px;
  background-color: #f5f5f5;
}

.dashboard-title {
  font-size: 24px;
  margin-bottom: 20px;
}

.filters {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}

.filters label {
  font-size: 16px;
  color: #333;
}

.filters input {
  padding: 5px;
  margin-left: 10px;
}

.chart-container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}
